'use strict';

let modalComponent = {
  title: 'Add New Todo',
  value: '',
  delete: false,
  renderHTML() {

    let form = '';
    // Switch UI out if delete set to true
    if (this.delete) {
      form = `
        <div class="input-container">
          <h2>Your are about to delete this item</h2>
          <input type="text" disabled id="input-add-todo" class="form-control" id="" value="${this.value}">
          <button type="button" id="btn-save-todo" class="btn btn-red">Confirm Delete</button>
        </div>
      `;
    } else {
      form = `
        <div class="input-container">
          <input type="text" placeholder="${this.title}" id="input-add-todo" class="form-control" value="${this.value}">
          <button type="button" id="btn-save-todo" class="btn btn-green">GO</button>
        </div>
      `;
    }

    const html = `
      <div class="modal" id="modal">
        <div class="modal-header">
          <h4 class="modal-title"></h4>
          <button type="button" class="close btn btn-red" id="btn-close-modal">
            <span aria-hidden="true">X</span>
          </button>
        </div>
        <div class="modal-body">
           ${form}
        </div>
      </div>
    `;

    return html.trim();
  }
};

function renderTable(data, active) {

  // Remove unpublished/deleted items from the array
  const dataIsolated = data.filter(p => p.publish);

  let rows = [];
  let countTotal = 0;
  let countComplete = 0;
  dataIsolated.map(items => {

    countTotal += 1; // total no of rows
    if (items.complete) countComplete += 1; // get total no of completed

    const editParams = [items.id, 1]; // edit
    const deleteParams = [items.id, 2]; // delete
    
    // edit and delete buttons
    const btns = `
      <button type="button" data-type="edit" onclick="renderModal(${editParams});" class="btn btn-green btn-edit">Edit</button>
      <button type="button" data-type="delete" onclick="renderModal(${deleteParams});" class="btn btn-red btn-delete">Delete</button>
    `;

    // checkbox statuses and pararms for priority
    const priority = (items.priority) ? ' checked' : '';
    const priorityText = (items.priority) ? ' Yes' : ' No';
    const priorityParams = [items.id, 1];
    // checkbox statuses and pararms for complete
    const complete = (items.complete) ? ' checked' : '';
    const completeText = (items.complete) ? ' Yes' : ' No';
    const completeParams = [items.id, 2];

    // generate table rows
    rows += `
      <tr>
        <td>${items.name}</td>
        <td>
          <label>
            <input type="checkbox"${priority} onchange="toggleHandler(${priorityParams});" class="custom-control-input">${priorityText}
          </label>
        </td>
        <td>
          <label>
            <input type="checkbox"${complete} onchange="toggleHandler(${completeParams});" class="custom-control-input">${completeText}
          </label>
        </td>
        <td>${btns}</td>
      </tr>
    `;  
  });

  // create button
  const addBtn = `
      <button type="button" id="btn-launch-modal" class="btn btn-green">Add New Todo</button>
  `;

  const sortPriorityParams = [1];
  const sortNameParams = [2];

  const sortPriority = (active.sortPriority) ? ' checked' : '';
  const sortName = (active.sortName) ? ' checked' : '';
 
  // sort UI
  const sortUI = `
    <div>
    <label style="margin-right: 1rem">
      <input type="checkbox"${sortName} id="checkbox-sort-name" name="checkbox-sort" onchange="sortHandler(${sortNameParams});" class="custom-control-input"> Sort by Name
    </label>
    <label>
      <input type="checkbox"${sortPriority} id="checkbox-sort-priority" name="checkbox-sort" onchange="sortHandler(${sortPriorityParams});" class="custom-control-input"> Sort by Priority
    </label>
    </div>
  `;

  // assemble the table
  const table = `
   <div class="ui-container">${sortUI} ${addBtn}</div>
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Priority</th>
          <th>Complete</th>
          <th>Edit</th>
        </tr>
      </thead>
      <tbody>
        ${rows}
      </tbody>
    </table>
    <div class="text-center" style="margin-top: 1rem">${countComplete} of ${countTotal} Todos Complete.</div>
  `;

  return table.trim();
}

let data = [
  {
    id: 1,
    name: 'Apply for position @ Rocket',
    priority: 1,
    complete: 1,
    publish: true
  },  {
    id: 2,
    name: 'Explain to 2 year old that not everything can be solved through kicking',
    priority: 0,
    complete: 1,
    publish: true
  }, {
    id: 3,
    name: 'Complete coding challenge',
    priority: 1,
    complete: 1,
    publish: true
  }, {
    id: 4,
    name: 'Tell 5 year old that saying \'Come at me bro!\' does not absolve you of anything',
    priority: 0,
    complete: 0,
    publish: true
  }, {
    id: 5,
    name: 'Start telling people about the sweet NFT\'s you have',
    priority: 0,
    complete: 1,
    publish: true
  }, {
    id: 6,
    name: 'Land dream job @ Rocket',
    priority: 1,
    complete: 0,
    publish: true
  } 
];

let active = {
  id: '',
  action: '',
  sortPriority: false,
  sortName: false
};

/* ########################
######### METHODS #########
######################### */

const closeModal = () => {
  // Remove modal from DOM
  const modal = document.getElementById('modal');
  modal.classList.remove("active");

  setTimeout(() => {
    modal.remove();
  }, 500);

  active.id = '';
  active.action = '';
};

const renderModal = (...params) => {

  console.log(params[0], params[1]);

  // params[1] === 1 Edit
  // params[1] === 2 Delete
 
  // Generate new modal object
  const modal = Object.create(modalComponent);
  modal.title = 'Add New Todo';

  // check params for edit or delete
  if (params[1]) {

    active.id = params[0];
    active.action = params[1];

    const val = data.filter(p => p.id === params[0]);
    modal.value = val[0].name;

    // set to delete to true to switch modal UI
    if (params[1] === 2) modal.delete = true;
  }

  const newModal = modal.renderHTML();
  const mount = document.getElementById('modal-mount-point');

  if (params[2]) {
    // return true function for tests
    return true;
  } else {
    // Add modal to the DOM
    mount.innerHTML = newModal;
  }


  if ((!params[1]) || (params[1] === 1)) {
    // Automatically add focus to the input in the modal
    const input = document.getElementById('input-add-todo');
    input.focus();

  }

  // add lister to save btn
  document.getElementById("btn-save-todo").addEventListener("click", saveValue);

  // Add listener to close modal button
  document.getElementById("btn-close-modal").addEventListener("click", closeModal);
  
  setTimeout(() => {
  
    const element = document.getElementById("modal");
    element.classList.add("active");

  }, 100);
};

const saveValue = (...params) => {
  
  let value = '';
  if (params[1]) {
    // set value when running tests
    value = 'Test item';
    active.action = '';
  } else {
    value = document.getElementById("input-add-todo").value;
  }

  // Delete function
  if (active.action === 2) {
    // isolate item to delete
    const dataIsolated = data.filter(p => p.id === active.id);
    // set item to unpublish (soft delete)
    dataIsolated[0].publish = false;

  // Edit function
  } else if (active.action === 1) {
    // Isolate item to edit
    const dataIsolated = data.filter(p => p.id === active.id);
    // Set new value
    dataIsolated[0].name = value;
  
  // Create new item function
  } else {
    
    let count = 0;

    data.map(() => { count += 1; });

    data.push({
      id: count + 1,
      name: value,
      priority: 0,
      complete: 0,
      publish: true
    });
  }

  active.id = '';
  active.action = '';

  console.log(data);

  if (params[1]) {
    // isolate new test item
    const testDataIsolated = data.filter(p => p.id === 7);
    // return out of function
    return testDataIsolated;
  } 

  closeModal();
  loadApp();
};

const toggleHandler = (...params) => {

  if (!params[1]) return;

  // params[1] === 1 Priority
  // params[1] === 2 Complete

  active.id = params[0];
  active.action = params[1];
  // Isolate item to edit
  const dataIsolated = data.filter(p => p.id === active.id);

  if (active.action === 1) {
    dataIsolated[0].priority = !dataIsolated[0].priority;
  } else {
    dataIsolated[0].complete = !dataIsolated[0].complete;
  }

  active.id = '';
  active.action = '';

  console.log(data, params[2]);
  
  // return true if running test
  if (params[2]) return true;

  loadApp();

};

const sortHandler = (...params) => {

  if (!params[0]) return;

  if (params[0] === 1) {

    active.sortPriority = !active.sortPriority;

    if (active.sortPriority) {
      // this method is taken from https://stackoverflow.com/questions/979256/sorting-an-array-of-objects-by-property-values
      data.sort((a, b) => parseFloat(b.priority) - parseFloat(a.priority));
    } else {
      data.sort((a, b) => parseFloat(a.priority) - parseFloat(b.priority));
    }

  } else {
    
    active.sortName = !active.sortName;

    if (active.sortName) {
      // this method is taken from https://stackoverflow.com/questions/1129216/sort-array-of-objects-by-string-property-value
      data.sort((a,b) => (a.name > b.name) ? 1 : ((b.name > a.name) ? -1 : 0));
    } else {
      data.sort((a,b) => (b.name > a.name) ? 1 : ((a.name > b.name) ? -1 : 0));
    }
  }

  console.log(active);

  // return true if running tests
  if (params[1]) return true;

  loadApp();

};


const loadApp = () => {

  active.id = '';
  active.action = '';
  
  const tableHTML = renderTable(data, active);
  const mount = document.getElementById('app-mount-point');

  mount.innerHTML = tableHTML;

  // Listen for add todo click
  document.getElementById("btn-launch-modal").addEventListener("click", renderModal);
      
  toggleHandler();

  sortHandler();

};

document.addEventListener("DOMContentLoaded", loadApp);
